import axios from "axios";
import jwt_decode from "jwt-decode";

const state = {
  user: null,
  token: null,
  role: null,
  isAdmin: false,
  profileImg: null,
};

const getters = {
  isAuthenticated: (state) => !!state.user,
  isAdmin: (state) => state.isAdmin,
  StateUser: (state) => state.user,
  StateToken: (state) => state.token,
  StateRole: (state) => state.role,
  ProfileImg: (state) => state.profileImg
};

const actions = {
  async Register({ dispatch }, form) {
    for (const key in form) {
      let value = form[key]
      if (value == "") {
        throw new Error(`Um dos campos está vazio, verifique!`)
      }
    }
    await axios.post('account/register', form)
    let UserForm = {
      "email": form.email,
      "password": form.password,
    }

    await dispatch('LogIn', UserForm)
  },

  async LogIn({ commit }, user) {
    if (user.email == "" || user.password == "") {
      throw new Error('Usuário ou senha não pode ser vazio')
    }
    let response = await axios.post("account/auth", user)
    if (response.status == 404) {
      throw new Error('Usuário não encontrado');
    }
    else if (response.status != 200) {
      throw new Error('Ocorreu um erro na API');
    }
    await commit("setUser", user.email);
    await commit("setToken", response.data);
  },

  async LogOut({ commit }) {
    await commit("logout");
  },

  async CreateAdmin({ commit }, userId) {
    let response = await axios.post("/admin/" + userId)
    if (response == null || response.status != 200) {
      throw new Error('Ocorreu um erro na API');
    }
    await commit("setToken", state.token)
  },

  async GetUsers() {
    let response = await axios.get("/clients?type=A")
    if (response == null || response.status == 404) {
      return []
    }
    else if (response.status != 200) {
      throw new Error('Ocorreu um erro na API');
    }
    return response.data
  },

  async GetClients() {
    let response = await axios.get("/clients?type=C")
    if (response == null || response.status == 404) {
      return []
    }
    else if (response.status != 200) {
      throw new Error('Ocorreu um erro na API');
    }
    return response.data
  },
};

const mutations = {
  setUser(state, username) {
    state.user = username;
  },
  setToken(state, token) {
    let decoded = jwt_decode(token);
    state.token = token;
    axios.defaults.headers.common['Authorization'] = `Bearer ${token}`
    state.role = decoded.role;
    if (state.role != null && Array.isArray(state.role)) {
      for (const role in state.role) {
        if (state.role[role] == "admin") {
          state.isAdmin = true
        }
      }
    }else if (state.role != null && state.role == "admin") {
      state.isAdmin = true
    }
    else {
      state.isAdmin = false
    }
    if ("profile" in decoded) {
      state.profileImg = decoded.profile
    }
    else {
      state.profileImg = "data:image/png;base64,"
    }
  },
  logout(state) {
    state.user = null;
    state.token = null;
    state.isAdmin = false;
    state.profileImg = "../assets/avatar-fallback.png";
    axios.defaults.headers.common['Authorization'] = null
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
};